(function () {
    'use strict';
    var angular = require('angular');
    require('./global-exception-handler.component.scss');

    var ctrl = function ($rootScope, $state) {

        var vm = this;

        vm.$onInit = function () {
            $rootScope.$broadcast("app-timeout");
        };

        vm.exit = function () {
            $state.go('app.journey.s1-cards');
        };
    };

    var globalExceptionHandlerComp = angular.module('app').component('globalExceptionHandler', {
        template: require('./global-exception-handler.component.html'),
        controller: ['$rootScope', '$state', ctrl]
    });

    module.exports = globalExceptionHandlerComp.name;
})();


